<template>
  <div class="pictureUpload">
    <div class="label">
      {{ labelTitle }}
    </div>
    <div class="content">
      <div class="description">{{ description }}</div>
      <div class="line">
        <div class="uploadImg" @click="btnClick">
          <input
            type="file"
            @change="onFileChange"
            id="imagePhotoUpload"
            v-show="false"
            accept="image/*"
          />
          <!-- <button @click="btnClick">点击上传</button> -->
          <img
            src="../../../../../assets/image/providerTools/uploadImage.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            点击上传
          </div>
        </div>
        <div class="right">
          <div class="picture" v-show="picture !== ''">
            <div
              style="
                width: 186px;
                height: 120px;
                overflow: hidden;
                position: relative;
              "
            >
              <img
                :src="picture"
                style="
                  position: absolute;
                  width: 186px;
                  margin: auto;
                  top: -9999px;
                  right: -9999px;
                  bottom: -9999px;
                  left: -9999px;
                "
              />
            </div>
            <div class="cancel" title="删除" @click="deletePicture()"></div>
          </div>
        </div>
      </div>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import { $uploadStreamMediaFile } from "../../../../../network/common/common";
import { readImageFileToDetectRatio } from "../../../../../common/utils";
export default {
  name: "e-imagePhotoUpload",
  props: {
    labelTitle: {
      type: String,
      default: "",
    },
    description: {
      type: String,
      default: "",
    },
  },

  data() {
    return {
      picture: "",
      originalPicture: "",
      targetData: "",
      isLoading: false,
      disable: false,
    };
  },
  methods: {
    btnClick() {
      if (this.disable) return;
      console.log("click 上传图片");
      document.getElementById("imagePhotoUpload").click();
    },
    async onFileChange(e) {
      const file_input = document.querySelector("#imagePhotoUpload");
      let files = e.target.files || e.dataTransfer.files;
      if (files.length === 0) return;
      const originalpic = files[0];
      // 加水印的图片ret

      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      if (!/image\/\w+/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求", 240);
        file_input.value = "";
        return;
      }
      // 判断图片大小是否小于等于2MB
      const size = originalpic.size / 1024; //单位：kb
      if (size > 2048) {
        this.$toast.error("图片大小超2MB", 240);
        file_input.value = "";
        return;
      }

      try {
        const res = await readImageFileToDetectRatio(files[0], 1.5);
        if (res.code !== "1111") {
          this.$toast.error("图片长宽比例不符要求", 240);
          file_input.value = "";
          this.isLoading = false;
          return;
        }
      } catch (err) {
        this.$toast.error("图片读取失败", 240);
        file_input.value = "";
        this.isLoading = false;
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);
      try {
        const response = await $uploadStreamMediaFile(formData);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.picture = details.image_url;
            this.$emit("updateCompsOffsetTop");
            this.$toast.success("图片上传成功", 240);
          } else {
            this.$toast.error("上传失败", 240);
          }
        } else {
          this.$toast.error("上传失败", 240);
        }

        file_input.value = "";
        setTimeout(() => {
          this.isLoading = false;
        }, 600);
      } catch (err) {
        this.$toast.error("上传失败", 240);
        file_input.value = "";
        this.isLoading = false;
      }
    },
    deletePicture() {
      this.picture = "";
    },
    showPicPreview(picture) {
      this.$picPreview.list = [picture];
      this.$picPreview.activeIndex = 0;
      this.$picPreview.ifShowPicPreview = true;
    },
  },
  watch: {
    picture(newVal) {
      if (newVal !== "") {
        this.$emit("setLegitimacyTrue");
      } else {
        this.$emit("setLegitimacyFalse");
      }

      if (newVal !== this.originalPicture) {
        this.$emit("setUpdateStatusTrue");
      } else {
        this.$emit("setUpdateStatusFalse");
      }
    },
  },
};
</script>

