          this.$refs[this.compList[6].referenceName].originalPictureList =
            window.JSON.parse(window.JSON.stringify(res.details.shows));
          this.$refs[this.compList[7].referenceName].pictureList =
            window.JSON.parse(window.JSON.stringify(res.details.honor));
          this.$refs[this.compList[7].referenceName].originalPictureList =
            window.JSON.parse(window.JSON.stringify(res.details.honor));

          this.ticketId = res.details.id;
          this.state = 2;
          this.mobile = res.details.mobile;
          this.email = res.details.email;
          this.wechat = res.details.wechat;

          for (const val of this.compList) {
            if (this.$refs[val.referenceName])
