      }
    } catch (err) {
      this.$toast.error("获取主理人数据失败2 catch 386", 240);
      console.log(22222222, err);
    }

    this.isLoading = false;
    this.timeoutLoadingToWait = () => {
      return new Promise((resolve) => {
        setTimeout(() => {
          this.isLoading = false;
          resolve();
        }, 1000);
      });
    };
    window.addEventListener("resize", () => {
      setTimeout(() => {
        for (const val of this.compList) {
          if (this.$refs[val.referenceName])
            this.$set(
              val,
              "offsetTop",
              this.$refs[val.referenceName].$el.offsetTop
