  //       this.$refs[this.compList[4].referenceName].picture = this.$refs[this.compList[4].referenceName].originalPicture;
  //       this.$refs[this.compList[5].referenceName].picture = this.$refs[this.compList[5].referenceName].originalPicture;
  //       this.$refs[this.compList[6].referenceName].pictureList = window.JSON.parse(window.JSON.stringify(this.$refs[this.compList[6].referenceName].pictureList));
