        );
        this.$set(
          this.compList[7],
          "offsetTop",
          this.$refs[this.compList[7].referenceName].$el.offsetTop
        );
      }, 500);
    },
    beforeUnloadHandler(e) {
      if (this.updateMark) {
        e.returnValue = "111111"; //不是null就可以了
      } else {
        return;
      }
    },
    checkstate() {
      let status = Boolean(this.updateMark && this.legitimacyMark);
      console.log(status);
      console.log("updatemark: ", this.updateMark);
      console.log("legit: ", this.legitimacyMark);
      let message = "";
      if (!status) {
        if (!this.updateMark) {
          console.log("not updated");
          message = "没有更新数据，无需再次提交审核";
        } else {
          console.log("not legit");
          console.log(this.compList);
          let newVal = [...this.compList];
          console.log(newVal);
          for (const item of newVal) {
            if (!item.valueLegitimacy) {
              console.log("not legit", item);
              console.log("not lefgit item: ", item);
              message =
                (item.chineseName || item.referenceName) + "填写出错，请检查";
              break;
            }
          }
        }
        this.$toast.warn(message, 0, 500, 3000);
      }
      return status;
    },
    async showEditReasonDialog() {
      //检查状态
      let state = this.checkstate();

      this.isLoading = true;
      await this.timeoutLoadingToWait();

      if (this.updateMark) {
        for (const val of this.compList) {
          if (val.valueLegitimacy === false) {
            document.body.scrollTop =
              this.$refs[val.referenceName].$el.offsetTop - 150;
            document.documentElement.scrollTop =
              this.$refs[val.referenceName].$el.offsetTop - 150;
            if (val.hasOwnProperty("errorHint")) {
              this.$toast.error(val.errorHint, 240);
            }
            await warnBling(this.$refs[val.referenceName].$el);
            return;
          }
        }
        this.ifShowEditReasonDialog = true;
      } else {
        this.$toast.warn("当前没有修改", 240);
      }
    },
    async showEditReasonDialogIfLegitimacy() {
      // this.isLoading = true;
      // await this.timeoutLoadingToWait();
      this.ifShowEditReasonDialog2 = true;
    },
    setShowsPictureErrorHint(errorHint) {
      this.$set(this.compList[6], "errorHint", errorHint);
    },
    setQualiHonorPictureErrorHint(errorHint) {
      this.$set(this.compList[7], "errorHint", errorHint);
    },
    async deployProviderProfileTicket(editReason) {
      if (editReason === "") {
        this.$toast.warn("请填写编辑理由", 240);
        console.log("情填写编辑理由 war了");
        console.log(this);
        return;
      }
      this.$refs.editReasonDialog.ifShowLoadingGif = true;
      //造请求负载
      const requestPayload = {
        user: {
          id: Number(window.localStorage.getItem("userID")), //5
        },
        version: "1.0.0",
        create_reason: editReason,
        work_line_id: 1500,
        work_operation: 4300,
        content: {
          //id: Number(window.localStorage.getItem('providerID')),//5
          user_id: Number(window.localStorage.getItem("userID")), //27
          name: this.$refs[this.compList[0].referenceName].inputText,
          describe: this.$refs[this.compList[1].referenceName].inputText,
          abbr: this.$refs[this.compList[3].referenceName].inputText,
          avatar_square: this.$refs[this.compList[4].referenceName].picture,
          portrait: this.$refs[this.compList[5].referenceName].picture,
          shows: this.$refs[this.compList[6].referenceName].pictureList,
          honor: this.$refs[this.compList[7].referenceName].pictureList,
          mobile: this.mobile,
          email: this.email,
          wechat: this.wechat,
        },
      };
      console.log(this.$refs[this.compList[1].referenceName]);
      console.log(requestPayload);
      window.localStorage.setItem(
        "unacceptedProfile",
        JSON.stringify(requestPayload)
      );

      try {
        const response = await $submitProviderProfileTicket(requestPayload);
        console.log(response);
        if (response.Code === 200 && response.msg === "success") {
          console.log("提交成功");
          console.log("jinlema");
          // this.$toast.success("提交成功，请等待审核", 1330)
          // this.$toast.warn("类别名不能为空", 240);
          this.$toast.success(
            "主理人信息提交成功，请等待审核，暂时不要重复提交",
            240,
            430,
            5000
          );
          // console.log(this.$toast)
          //  this.$toast.success("获取产品列表成功", 240);
          this.reset();
        } else {
          this.$toast.error("提交失败", 240);
        }
      } catch (err) {
        this.$toast.error("提交失败", 240);
      }
      this.$refs.editReasonDialog.ifShowLoadingGif = false;
      this.ifShowEditReasonDialog = false;
    },
    async providerProfileLaunchTicket(editReason) {
      if (editReason === "") {
        this.$toast.warn("请填写编辑理由", 240);
        return;
      }
      this.$refs.editReasonDialog2.ifShowLoadingGif = true;

      //造请求负载
      const requestPayload = {
        user: {
          id: Number(window.localStorage.getItem("userID")), //5
        },
        version: "1.0.0",
        create_reason: editReason,
        work_line_id: 1500,
        work_operation: 4600,
        content: {
          id: Number(this.ticketId),
        },
      };

      // console.log(requestPayload);

      try {
        const response = await $submitProviderProfileTicket(requestPayload);
        if (response.Code === 200 && response.msg === "success") {
          this.$toast.success("上架申请提交成功，请等待审核", 240, 430);
          this.reset();
        } else {
          this.$toast.error("提交失败", 240);
        }
      } catch (err) {
        this.$toast.error("提交失败", 240);
      }
      this.$refs.editReasonDialog2.ifShowLoadingGif = false;
      this.ifShowEditReasonDialog2 = false;
    },
