      this.$refs[this.compList[5].referenceName].disable = false;
      this.$refs[this.compList[6].referenceName].disable = false;
      this.$refs[this.compList[7].referenceName].disable = false;
    },
    reset() {
      this.$refs[this.compList[0].referenceName].inputText =
        this.$refs[this.compList[0].referenceName].originalInputText;
      this.$refs[this.compList[1].referenceName].inputText =
        this.$refs[this.compList[1].referenceName].originalInputText;
      this.$refs[this.compList[3].referenceName].inputText =
        this.$refs[this.compList[3].referenceName].originalInputText;
      this.$refs[this.compList[4].referenceName].picture =
        this.$refs[this.compList[4].referenceName].originalPicture;
      this.$refs[this.compList[5].referenceName].picture =
        this.$refs[this.compList[5].referenceName].originalPicture;
      this.$refs[this.compList[6].referenceName].pictureList =
        window.JSON.parse(
          window.JSON.stringify(
            this.$refs[this.compList[6].referenceName].originalPictureList
          )
        );
      this.$refs[this.compList[7].referenceName].pictureList =
        window.JSON.parse(
          window.JSON.stringify(
            this.$refs[this.compList[7].referenceName].originalPictureList
          )
        );
    },
  },
  watch: {
    compList: {
      handler(newVal) {
        // console.log('handler:',newVal)
        // console.log(JSON.stringify(newVal))
        let flag = false;
        for (const item of newVal) {
          if (item.updateStatus) {
            flag = true;
          }
        }

        this.updateMark = flag;

        let flag2 = true;
        for (const item of newVal) {
          if (!item.valueLegitimacy) {
            // console.log('not legit',item)
            flag2 = false;
          }
        }
        this.legitimacyMark = flag2;
        let is_active = this.hostRequestIsActive;

        if (is_active) {
          this.updateMark = false;
          this.legitimacyMark = false;
        }
      },
      deep: true,
    },
    state(newVal) {
      switch (newVal) {
        case 1:
          break;
        case 2:
          this.reset();
          this.disableAll();
          break;
        case 3:
          this.reableAll();
          break;
      }
    },
  },
  components: {
    GeneralFormTextInput,
    TextareaZone,
    RadioSingleSelect,
    ClickToSelect,
    AvatarPictureUpload,
    ImagePhotoUpload,
    ShowsPictureUpload,
    QualiHonorPicUpload,
    ItemSelectDialog,
    EditReasonDialog,
  },
};
</script>

