<template>
  <div
    id="item-select-dialog"
    :style="{ width: dialogWidth + 'px', height: dialogHeight + 'px' }"
  >
    <p class="imageZone" @click="hideDialog" title="点击隐藏"></p>
    <p class="guideTitle">{{ guideTitle }}</p>
    <div class="labelZone">
      <p
        v-for="(item, index) in list"
        :key="item.id + index"
        @click="itemClick(index)"
        :style="item.selected ? { backgroundColor: 'rgba(1, 1, 1, .1)' } : {}"
      >
        {{ item.name }}
      </p>
    </div>
    <div class="buttonZone">
      <button @click="cancelAllSelected">清除</button>
      <button
        @click="hideDialog"
        style="
          background-color: var(--color-background-button-green);
          color: var(--color-impure-white);
          margin-left: 40px;
        "
      >
        确认
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ItemSelectDialog",
  props: {
    // 待选的【活动特色】列表
    list: {
      type: Array,
      default() {
        return [];
      },
    },
    guideTitle: {
      type: String,
      default: "",
    },
    dialogWidth: {
      type: Number,
      default: 600,
    },
    dialogHeight: {
      type: Number,
      default: 300,
    },
  },
  methods: {
    // 点击弹框右上角的X图片时触发该方法的执行
    hideDialog() {
      this.$emit("hideDialog");
    },
    itemClick(index) {
      this.$emit("itemClick", index);
    },
    cancelAllSelected() {
      this.$emit("cancelAllSelected");
    },
  },
};
</script>

