<template>
  <div class="pictureUpload">
    <div class="label">
      {{ labelTitle }}
    </div>
    <div class="content">
      <div class="description">{{ description }}</div>
      <div class="line">
        <div
          class="uploadImg"
          @click="btnClick"
          v-show="pictureList.length < 4"
        >
          <input
            type="file"
            @change="onFileChange"
            id="showsPictureUpload"
            v-show="false"
            accept="image/*"
            multiple
          />
          <!-- <button @click="btnClick">点击上传</button> -->
          <img
            src="../../../../../assets/image/providerTools/uploadImage.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            点击上传
          </div>
        </div>

        <div class="right-list">
          <div
            v-for="(pic, index) in pictureList"
            :key="pic.url"
            class="right-item"
          >
            <div style="display: flex; flex-direction: column">
              <img :src="pic.url" style="width: 186px; height: 120px" />
              <input
                v-show="showname"
                class="pictureNameInput"
                type="text"
                v-model="pic.name"
                placeholder="请输入图片展示名称"
              />
            </div>
            <!-- <a-tooltip placement="topLeft">
              <template #title
                >当前图片展示名称：{{
                  pic.name === "" ? "#empty" : pic.name
                }}，可在下方输入框输入字符修改图片展示名称</template
              >
              <p class="pictureName">请填写图片的展示名称</p>
            </a-tooltip> -->

            <a-tooltip placement="left">
              <!-- <template #title>删除本张图片</template> -->
              <div
                class="cancel"
                title="删除"
                @click="deletePicture(index)"
              ></div>
            </a-tooltip>
          </div>
        </div>
      </div>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import { $uploadStreamMediaFile } from "../../../../../network/common/common";
import {
  readImageFileToDetectRatio,
  uniqueArr,
  arrayDeepEqualDetect,
  deleteFileNameSuffix,
} from "../../../../../common/utils";
import Sortable from "sortablejs";
export default {
  name: "showsPictureUpload",
  mounted() {
    const $ul = this.$el.querySelector(".right-list");
    this.sortList = Sortable.create($ul, {
      handle: ".right-item",
      draggable: ".right-item",
      animation: 250,
      onUpdate: (event) => {
        const newIndex = event.newIndex;
        const oldIndex = event.oldIndex;
        const $li = $ul.children[newIndex];
        const $oldLi = $ul.children[oldIndex];
        // 先删除移动的节点
        $ul.removeChild($li);
        // 再插入移动的节点到原有节点，还原了移动的操作
        if (newIndex > oldIndex) {
          $ul.insertBefore($li, $oldLi);
        } else {
          $ul.insertBefore($li, $oldLi.nextSibling);
        }
        const item = this.pictureList[event.oldIndex];
        this.pictureList.splice(event.oldIndex, 1);
        this.pictureList.splice(event.newIndex, 0, item);
      },
    });
  },
  props: {
    labelTitle: {
      type: String,
      default: "",
    },
    description: {
      type: String,
      default: "",
    },
    showname: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      pictureList: [],
      originalPictureList: [],

      targetData: "",

      sortList: Object,

      isLoading: false,
      disable: false,
    };
  },
  methods: {
    btnClick() {
      if (this.disable) return;
      // 在测试后，把风采图片数量改为不设限
      /* if(this.pictureList.length >= 4) {
        this.$toast.error('图片数量已达上限', 240);
        return;
      } */
      console.log("ShowsPicureUpload.vue");
      document.getElementById("showsPictureUpload").click();
    },
    async onFileChange(e) {
      const file_input = document.querySelector("#showsPictureUpload");
      let files = e.target.files || e.dataTransfer.files;
      if (files.length === 0) return;
      const originalpic = files[0];
      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      if (!/image\/\w+/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求", 240);
        file_input.value = "";
        return;
      }

      // 判断图片大小是否小于等于2MB
      const size = originalpic.size / 1024; //单位：kb
      if (size > 2048) {
        this.$toast.error("图片大小超2MB", 240);
        file_input.value = "";
        return;
      }

      try {
        const res = await readImageFileToDetectRatio(files[0], 1.5, 0.5);
        if (res.code !== "1111") {
          this.$toast.error("图片长宽比例不符要求", 240);
          file_input.value = "";
          this.isLoading = false;
          return;
        }
      } catch (err) {
        this.$toast.error("图片读取失败", 240);
        file_input.value = "";
        this.isLoading = false;
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);
      try {
        const response = await $uploadStreamMediaFile(formData);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.pictureList.push({
              name: deleteFileNameSuffix(files[0].name),
              url: details.image_url,
            });
            this.$emit("updateCompsOffsetTop");
            this.$toast.success("图片上传成功", 240);
          } else {
            this.$toast.error("上传失败", 240);
          }
        } else {
          this.$toast.error("上传失败", 240);
        }

        file_input.value = "";
        setTimeout(() => {
          this.isLoading = false;
          // this.$toast.success('图片上传成功',)
        }, 600);
      } catch (err) {
        this.$toast.error("上传失败", 240);
        file_input.value = "";
        this.isLoading = false;
      }
    },
    deletePicture(index) {
      this.pictureList.splice(index, 1);
    },
    showPicPreview(list, index) {
      this.$picPreview.list = list;
      this.$picPreview.activeIndex = index;
      this.$picPreview.ifShowPicPreview = true;
    },
  },
  watch: {
    pictureList: {
      handler(newVal) {
        // 先比较是否有更新
        const f = arrayDeepEqualDetect(
          newVal,
          window.JSON.parse(window.JSON.stringify(this.originalPictureList))
        );
        if (f) {
          this.$emit("setUpdateStatusFalse");
        } else {
          this.$emit("setUpdateStatusTrue");
        }

        // 先判断图片的张数
        if (newVal.length < 2) {
          this.$emit("setLegitimacyFalse");
          this.$emit("setErrorHint", "图片数量不能少于2张");
          return;
        }

        // 判断图片展示名称是否有为空的
        for (const item of newVal) {
          if (item.name === "") {
            this.$emit("setLegitimacyFalse");
            this.$emit("setErrorHint", "图片名字不能为空");
            return;
          }
        }

        // 判断图片的名称是否有相同的
        const tempArray = [];
        for (const item of newVal) {
          tempArray.push(item.name);
        }
        const newArray = uniqueArr(tempArray);
        if (tempArray.length !== newArray.length) {
          this.$emit("setLegitimacyFalse");
          this.$emit("setErrorHint", "图片名字不能重复");
          return;
        }

        this.$emit("setLegitimacyTrue");
        this.$emit("setErrorHint", "");
      },
      deep: true,
    },
  },
  components: {},
};
</script>

