  <!-- 表单项中的点击选择 -->
<template>

  <div class="clickToSelect">
    <div class="label">
      {{labelTitle}}
    </div>
    <div class="inputZone">
      <div class="clickSelectZone "
           @click="keyTextClick"
           ref="clickToSelect">{{showText}}</div>
      <div class="storeSelectedCategory">
        <div v-for="(item, index) in selectedList"
             :key="item.id"
             class="storeSelectedCategoryItem">
          <p>{{item.name}}</p>
          <p class="imageZone"
             @click="selectedItemClick(index)"
             :title="`删除【${item.name}】选择项`"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-ClickToSelect',
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: '',
    },
    selectedList: {
      type: Array,
      default() {
        return [];
      },
    },
    activityStatus: {
      type: String,
      default: '0000',
    },
  },
  data() {
    return {
      showText: '点击选择',
    }
  },
  methods: {
    // 【点击选择/点击隐藏】文字区域被点击时触发该方法的执行
    keyTextClick() {
      this.$emit('changeDialogStatus');
    },
    // 点击被选择并展示到页面上的活动类别item的右上角的X时触发该方法的执行
    selectedItemClick(index) {
      this.$emit('deleteSelectedItem', index);
    },
  },
}
</script>

