<template>
  <div class="radioInputZone">
    <div class="label">
      <label>{{ labelTitle }}</label>
    </div>
    <div class="radioInputs">
      <div class="radio" v-for="(item, index) in radios" :key="item.id">
        <label
          >{{ item.label
          }}<input
            type="radio"
            :value="item.value"
            v-model="selected"
            v-show="false"
        /></label>
        <div class="substitute-element-border" @click="clickToSelect(index)">
          <span
            class="substitute-element"
            :style="
              selected === item.value
                ? {
                    backgroundColor: 'var(--color-background-button-green)',
                    border: 'none',
                  }
                : {}
            "
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-RadioSingleSelect",
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: "",
    },
    // 待选的单选框列表
    radios: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      selected: null,
      originalSelected: null,

      targetData: null,
    };
  },
  methods: {
    clickToSelect(index) {
      this.selected = this.radios[index].value;
    },
  },
  watch: {
    selected(newVal) {
      this.$emit("setLegitimacyTrue");
      //console.log(true === null) //false

      if (newVal !== this.originalSelected) {
        this.$emit("setUpdateStatusTrue");
      } else {
        this.$emit("setUpdateStatusFalse");
      }

      this.targetData = newVal;
    },
  },
};
</script>

